import { useEffect, useState } from 'react';
import { UserListApi, UserBanApi } from 'utils/Api';
import { Table, Button, Popconfirm } from 'antd';
import { useNavigate } from 'react-router-dom';
const { Column } = Table;

const User = () => {
  const navigate = useNavigate();
  const [tableData, setTableData] = useState();
  const [filters, setFilters] = useState();

  useEffect(() => {
    UserListApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          createdAt: item.createdAt
            .replace(/[TZ]/g, ' ')
            .substr(0, item.createdAt.length - 5),
          updatedAt: item.updatedAt
            .replace(/[TZ]/g, ' ')
            .substr(0, item.createdAt.length - 5),
        }))
      );
      setFilters(
        res.data.map((item) => ({
					text: item.phone,
					value: item.phone
        }))
      );
    });
  }, []);

  const ban = (record) => {
    UserBanApi(record._id).then(() => {
      const swapArr = tableData.map((item) =>
        item.id === record.id ? { ...item, state: !item.state } : item
      );
      setTableData(swapArr);
    });
  };

  return (
    <>
      <Table dataSource={tableData} rowKey="_id">
        <Column
          title="手机号码"
          dataIndex="phone"
          filters={filters}
          onFilter={(value, record) => record.phone === value}
        />
        <Column title="会员等级" dataIndex="level" />
        <Column
          title="推荐人"
          dataIndex="referrer"
          render={(_, record) =>
            record.referrer && <p>{record.referrer.phone}</p>
          }
        />
        <Column title="直推人数" dataIndex="inferior" />
        <Column
          title="状态"
          render={(_, record) => (record.state ? <p>正常</p> : <p>已封停</p>)}
        />
        <Column title="注册时间" dataIndex="createdAt" />
        <Column title="最后在线" dataIndex="updatedAt" />
        <Column
          title="钱包"
          dataIndex=""
          render={(_, record) => (
            <div className="flex gap-1">
              <Button
                type="primary"
                onClick={() => navigate('/user/wallet/' + record._id)}
              >
                查看
              </Button>
            </div>
          )}
        />
        <Column
          title="银行卡"
          dataIndex=""
          render={(_, record) => (
            <Button
              type="primary"
              onClick={() => navigate('/user/bank/' + record._id)}
            >
              查看
            </Button>
          )}
        />
        <Column
          title="操作"
          dataIndex=""
          render={(_, record) => (
            <div className="flex gap-1">
              <Popconfirm
                title={`你确定要${record.state ? '封禁' : '解封'}此用户?`}
                onConfirm={() => ban(record)}
                okType="link"
                cancelText="取消"
                okText="确认"
              >
                <Button type="primary" danger>
                  {record.state ? '封禁' : '解封'}
                </Button>
              </Popconfirm>
            </div>
          )}
        />
      </Table>
    </>
  );
};

export default User;
